a {
    color: inherit;
    text-decoration: none;
}

#header {
    z-index: 10;
    position: sticky;
    top: 0;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    background: rgb(254,107,139);
    background: -moz-linear-gradient(30deg, rgba(254,107,139,1) 10%, rgba(83,225,255,1) 90%);
    background: -webkit-linear-gradient(30deg, rgba(254,107,139,1) 10%, rgba(83,225,255,1) 90%);
    background: linear-gradient(30deg, rgba(254,107,139,1) 10%, rgba(83,225,255,1) 90%);
}

#navbar {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    overflow: hidden;
}

#navbar > a > li {
    padding: 15px;
    cursor: pointer;
}

#navbar > a > li > span {
    vertical-align: middle;
}

#navbar > a:hover {
    background: rgba(10, 10, 10, 0.1);
}

#header > #tel {
    display: none;
}

#lang {
    padding: 15px;
    cursor: pointer;
}

@media screen and (max-width: 767px) {
    #navbar {
        display: none !important;
    }

    #tel {
        display: block !important;
        text-align: start;
        margin: 0;
        width: 100%;
    }

    #tel > button {
        padding: 14px;
    }

    #tel_navbar {
        list-style-type: none;
        margin: 0;
        padding: 0;
        display: flex;
        flex-direction: column;
    }
    
    #tel_navbar > a > li {
        padding: 10px 15px;
        cursor: pointer;
    }

    #tel_navbar > a > li > span {
        vertical-align: middle;
    }

    #tel_navbar > a:hover {
        background: rgba(10, 10, 10, 0.1);
    }

    #lang {
        margin: 0 0 auto auto;
        position: absolute;
    }
}