.path {
    text-align: start;
    background-color: white;
    max-width: 768px;
    margin: 10% auto;
    border-radius: 12px;
    padding: 24px;
    word-wrap: break-word;
}

.path .timeline-item {
    position: relative;
}

.path .timeline-item:not(:last-child) {
    margin-bottom: 1.5rem;
}

.path .timeline-text .title {
    font-size: 5rem;
    font-family: "Playfair Display", serif;
    font-weight: 700;
    line-height: 1.2;
    color: #444;
    margin-bottom: 1rem;
}

.path .timeline-text .subtitle:not(:empty) {
    display: flex;
    font-size: 2.5rem;
    font-family: "Playfair Display", serif;
    font-weight: 700;
    line-height: 1.2;
    color: #444;
    margin-bottom: 1rem;
}
.path .timeline-text .subtitle:not(:empty):before {
    content: "-";
    font-size: 2rem;
    margin: auto 1rem auto 0.5rem;
}

.path .timeline-text .text {
    font-size: 1.1rem;
    line-height: 1.6;
    color: #888;
    margin: 0 auto;
    font-family: "Source Sans Pro", sans-serif;
}

.path .timeline-date {
    margin-bottom: 1rem;
}

.path .timeline-date .listdate {
    display: flex;
    flex-direction: row;
    list-style: none;
    margin: 0;
}

.path .timeline-date .listdate > li {
    margin-top: auto;
}

.path .timeline-date .line {
    z-index: 0;
    position: relative;
    top: -8px;
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background: rgb(174,213,129);
    background: -moz-linear-gradient(90deg, rgb(254, 107, 139) 0%, rgb(244, 114, 146) 45px, rgba(211,136,168,1) 90px, rgb(212, 212, 212) 91px, rgb(212, 212, 212) 90%, rgba(212, 212, 212, 0.5) 95%, rgba(212, 212, 212, 0) 100%);
    background: -webkit-linear-gradient(90deg, rgb(254, 107, 139) 0%, rgb(244, 114, 146) 45px, rgba(211,136,168,1) 90px, rgb(212, 212, 212) 91px, rgb(212, 212, 212) 90%, rgba(212, 212, 212, 0.5) 95%, rgba(212, 212, 212, 0) 100%);
    background: linear-gradient(90deg, rgb(254, 107, 139) 0%, rgb(244, 114, 146) 45px, rgba(211,136,168,1) 90px, rgb(212, 212, 212) 91px, rgb(212, 212, 212) 90%, rgba(212, 212, 212, 0.5) 95%, rgba(212, 212, 212, 0) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#aed581",endColorstr="#d4d4d4",GradientType=1);
}

.path .timeline-date .dateButton {
    padding-bottom: 15px;
    z-index: 1;
    width: 100px;
    max-width: 100px;
}

.path .timeline-date .dateButton::after {
    white-space: pre;
    position: absolute;
    top: calc(100% - 13px);
    height: 9px;
    width: 9px;
    content: "";
    background-color: white;
    border: 2px solid #333;
    border-radius: 100%;
}

.path .timeline-date .dateButton.active::after {
    border-color: rgb(0, 132, 158);
    background-color: rgb(0, 211, 253) ;
}

.path .mobile_previous, .path .mobile_after {
    display: none;
}

@media screen and (max-width: 767px) {
    .path {
        margin: 10% 20px;
    }

    .path .timeline-date .listdate li:not(.mobileshow) {
        display: none;
    }

    .path .timeline-date .listdate li {
        min-height: 59px;
        display: flex;
        margin: 0;
    }

    .path .listdate {
        justify-content: center;
        padding: 0;
    }

    .path .timeline-date .listdate .dateButton {
        height: 59px;
    }

    .path .timeline-date .listdate .mobile_active .dateButton::after {
        top: calc(100% - 10px);
        content: attr(data-text);
        font-weight: normal;
        font-size: 10px;
    }

    .path .timeline-date .listdate .dateButton::after {
        background: none;
        border: none;
    }

    .path .timeline-date .line {
        display: none;
    }

    .path .timeline-text .title {
        font-size: 3rem;
    }
    .path .timeline-text .subtitle {
        font-size: 1.5rem;
    }

    .path .mobileshow .dateButton {
        margin: auto 0;
        padding: 0;
    }

    .path .mobile_previous, .path .mobile_after {
        display: block;
        position: absolute;
        margin: 5px 0;
    }

    .path .mobile_previous {
        left: calc(50% - 184px);
    }

    .path .mobile_after {
        right: calc(50% - 184px);
    }

    .path .mobile_li.mobileshow {
        content: "";
        display: block;
        width: 100px;
        max-width: 100px;
    }

    .path .mobile_active .dateButton {
        font-weight: bold;
    }
}