.aboutme.card {
    max-width: 343px;
    margin: 5% auto 10%;
    border-radius: 12px;
    padding: 12px;
}

.aboutme .card-media {
    border-radius: 6px;
    width: 100%;
    height: 0;
    padding-bottom: 75%;
    background-color: rgba(0, 0, 0, 0.08);
    background-position: top;
}

.aboutme .title {
    text-align: center;
    font-size: 28px;
    line-height: 2;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    margin-bottom: 0.72em;
}

.aboutme .title:after {
    content: "";
    width: 36px;
    height: 2px;
    background-color: #ddd;
    display: block;
    margin: 8px auto;
    border-radius: 2px;
}

.aboutme .text {
    text-align: center;
    font-size: 14px !important;
    color: #222;
    line-height: 1.75 !important;
    width: 88%;
    margin: 0 auto !important;
}

.aboutme .separator:after {
    content: "";
    width: calc(100% + 56px);
    left: -28px;
    height: 2px;
    background-color: rgb(184, 184, 184);
    display: block;
    margin: 2rem auto 1.5rem;
    position: relative;
}